<template>
  <div id="el-table">
    <!-- 学年学期选择 -->
    <div id="class-list">
      <el-row>
        <el-col :span="6"
          ><div>
            <el-select
              v-model="value"
              placeholder="请选择"
              @change="filterYear()"
            >
              <el-option
                v-for="item in options"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select></div
        ></el-col>
      </el-row>
    </div>

    <el-row>
      <el-col :span="12">
        <div id="el-row">
          <!-- 增改导 -->
          <el-row>
            <el-button type="primary" @click="addData">新增</el-button>
            <el-button type="info">导入</el-button>
            <el-button type="info">导出</el-button>
            <el-button @click="removeMulSelectData">删除</el-button>
          </el-row>
        </div></el-col
      >

      <el-col :span="12">
        <!-- 筛选 -->
        <div id="filter-data">
          <el-select
            v-model="filters.sex"
            placeholder="性别"
            style="width: 100px"
            clearable
          >
            <el-option label="男" value="1"></el-option>
            <el-option label="女" value="2"></el-option>
          </el-select>
          <el-select
            v-model="filters.grade"
            placeholder="年级"
            style="width: 100px"
            clearable
          >
            <el-option label="高一" value="高一"></el-option>
            <el-option label="高二" value="高二"></el-option>
            <el-option label="高三" value="高三"></el-option>
          </el-select>
          <el-select
            v-model="filters.class"
            placeholder="班级"
            style="width: 100px"
            clearable
          >
            <el-option label="一班" value="一班"></el-option>
            <el-option label="二班" value="二班"></el-option>
            <el-option label="三班" value="三班"></el-option>
            <el-option label="四班" value="四班"></el-option>
            <el-option label="-" value="未分班"></el-option>
          </el-select>
          <el-input
            class="el-select"
            v-model="filters.nameAndCard"
            placeholder="输入学生姓名或学籍号"
            style="width: 200px"
          ></el-input>
          <el-button type="primary" plain>查询</el-button>
          <el-button type="info" plain>重置</el-button>
        </div>
      </el-col>
    </el-row>

    <!-- 表格 -->
    <el-table
      :data="
        tableData.slice((currentPage - 1) * pageSize, currentPage * pageSize)
      "
      stripe
      id="list"
      border
      :header-cell-style="{ background: '#d9ecff', color: '#606266' }"
      @selection-change="selectionChange"
    >
      <el-table-column type="selection" width="55" align="center">
      </el-table-column>
      <el-table-column label="序号" align="center" width="60">
        <template slot-scope="scope">{{ scope.$index + 1 }}</template>
      </el-table-column>
      <el-table-column
        prop="name"
        label="姓名"
        min-width="15%"
        sortable
      ></el-table-column>
      <el-table-column
        prop="studentCard"
        label="学籍号"
        min-width="22%"
      ></el-table-column>
      <el-table-column
        prop="startSchool"
        label="入学日期"
        min-width="16%"
        sortable
      >
      </el-table-column>
      <el-table-column prop="sex" label="性别" min-width="10%">
      </el-table-column>
      <el-table-column prop="age" label="年龄" min-width="10%" sortable>
      </el-table-column>
      <el-table-column prop="grade" label="年级" min-width="10%">
      </el-table-column>
      <el-table-column prop="class" label="班级" min-width="10%">
      </el-table-column>
      <el-table-column label="操作" align="center" width="140">
        <template slot-scope="scope">
          <!-- <el-button
            type="info"
            icon="el-icon-document"
            circle
            title="详情"
            size="small"
          ></el-button> -->
          <el-button
            type="primary"
            icon="el-icon-edit"
            circle
            title="编辑"
            size="small"
            @click="editData(scope.row, scope.$index)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-delete"
            circle
            title="删除"
            size="small"
            @click="removeData(scope.$index)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <div id="pagination">
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="pageSizes"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="tableData.length"
      >
      </el-pagination>
    </div>

    <!-- 新增编辑 -->
    <el-dialog
      :title="titleMsg"
      :visible.sync="dialogVisible"
      width="45%"
      :before-close="handleClose"
    >
      <div>
        <el-form
          :model="editObj"
          :rules="rules"
          ref="editObj"
          label-width="100px"
          class="demo-editObj"
        >
          <el-row :gutter="8">
            <!-- 第一行 -->
            <el-col :span="10">
              <el-form-item label="学生姓名" prop="name">
                <el-input v-model="editObj.name"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2"><div style="height: 36px"></div></el-col>

            <el-col :span="10">
              <el-form-item label="学籍号" prop="studentCard">
                <el-input v-model="editObj.studentCard"></el-input>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第二行 -->
          <el-row :gutter="8">
            <el-col :span="10">
              <el-form-item label="入学日期" prop="startSchool">
                <el-date-picker
                  type="date"
                  value-format="yyyy-MM-dd"
                  placeholder="选择日期"
                  v-model="editObj.startSchool"
                  style="width: 100%"
                >
                </el-date-picker>
              </el-form-item>
            </el-col>

            <el-col :span="2"><div style="height: 36px"></div></el-col>

            <el-col :span="10">
              <el-form-item label="性别" prop="sex">
                <el-select v-model="editObj.sex" placeholder="请选择">
                  <el-option label="男" value="男"></el-option>
                  <el-option label="女" value="女"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第三行 -->
          <el-row :gutter="8">
            <el-col :span="10">
              <el-form-item label="年龄" prop="age">
                <el-input v-model.number="editObj.age"></el-input>
              </el-form-item>
            </el-col>

            <el-col :span="2"><div style="height: 36px"></div></el-col>

            <el-col :span="10">
              <el-form-item label="年级" prop="grade">
                <el-select v-model="editObj.grade" placeholder="请选择">
                  <el-option label="高一" value="高一"></el-option>
                  <el-option label="高二" value="高二"></el-option>
                  <el-option label="高三" value="高三"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
          <!-- 第四行 -->
          <el-row :gutter="8">
            <el-col :span="10">
              <el-form-item label="班级" prop="class">
                <el-select v-model="editObj.class" placeholder="请选择" clearable>
                  <el-option label="一班" value="一班"></el-option>
                  <el-option label="二班" value="二班"></el-option>
                  <el-option label="三班" value="三班"></el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="sbumitData('editObj')"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  components: {},
  data() {
    // 自定义验证
    var checkAge = (rule, value, callback) => {
      if (!value) {
        return callback(new Error("请输入年龄"));
      }
      setTimeout(() => {
        if (!Number.isInteger(value)) {
          callback(new Error("请输入数字值"));
        } else {
          if (value > 20 || value < 6) {
            callback(new Error("必须为6~20岁"));
          } else {
            callback();
          }
        }
      }, 100);
    };
    return {
      dialogVisible: false,
      currentIndex: null,
      titleMsg: "",
      isAdd: true,
      multipleSelection: [],
      // 默认显示第几页
      currentPage: 1,
      // 总条数，根据接口获取数据长度(注意：这里不能为空)
      pageSizes: [10, 20, 50, 100],
      // 默认每页显示的条数（可修改）
      pageSize: 10,
      //筛选储存数据
      filters: {
        sex: "",
        grade: "",
        class: "",
        nameAndCard: "",
      },
      // 校验数据字段
      options: [
        {
          value: "选项3",
          label: "2018~2019学年 上学期",
        },
        {
          value: "选项2",
          label: "2017~2018学年 下学期",
        },
        {
          value: "选项1",
          label: "2017~2018学年 上学期",
        },
      ],
      // 学年学期
      label: "2018~2019学年 上学期",
      value: "选项3",
      // 校验规则
      rules: {
        name: [
          { required: true, message: "请输入学生名称", trigger: "blur" },
          {
            min: 2,
            max: 10,
            message: "长度在 2 到 10 个字符",
            trigger: "blur",
          },
        ],
        studentCard: [
          { required: true, message: "请输入学籍号", trigger: "blur" },
          {
            min: 16,
            max: 16,
            message: "长度为16个字符",
            trigger: "blur",
          },
        ],
        startSchool: [
          {
            type: "string",
            required: true,
            message: "请选择日期",
            trigger: "change",
          },
        ],
        sex: [{ required: true, message: "请选择性别", trigger: "change" }],
        age: [
          // { required: true, message: "请输入年龄", trigger: "blur" },
          { validator: checkAge, required: true, trigger: "blur" },
        ],
        grade: [{ required: true, message: "请选择年级", trigger: "change" }],
      },
      // 列表展示数据
      tableData: [
        {
          name: "安小然",
          studentCard: "1033963319103782",
          startSchool: "2019-03-03",
          sex: "女",
          age: 15,
          grade: "高二",
          class: "1班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "王小二",
          studentCard: "1033963319137912",
          startSchool: "2019-03-02",
          sex: "男",
          age: 16,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "王小虎",
          studentCard: "1033963311137913",
          startSchool: "2019-03-08",
          sex: "男",
          age: 17,
          grade: "高一",
          class: "2班",
          year: "2017~2018学年 下学期",
        },
        {
          name: "卜蓉",
          studentCard: "1033968319137824",
          startSchool: "2019-03-01",
          sex: "女",
          age: 16,
          grade: "高二",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "李绍勤",
          studentCard: "1033953319137925",
          startSchool: "2019-04-01",
          sex: "男",
          age: 15,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "李绍勤",
          studentCard: "1033963419137925",
          startSchool: "2019-04-01",
          sex: "男",
          age: 15,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "邹华阳",
          studentCard: "1113963319137925",
          startSchool: "2019-04-01",
          sex: "男",
          age: 15,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "陆妮曼",
          studentCard: "1033963319137985",
          startSchool: "2019-04-03",
          sex: "女",
          age: 15,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "李绍勤",
          studentCard: "1033463319137925",
          startSchool: "2019-04-01",
          sex: "男",
          age: 15,
          grade: "高一",
          class: "2班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "舒圆",
          studentCard: "1032963319137925",
          startSchool: "2019-03-28",
          sex: "女",
          age: 18,
          grade: "高三",
          class: "1班",
          year: "2018~2019学年 上学期",
        },
        {
          name: "罗孝忠",
          studentCard: "1013963319337972",
          startSchool: "2019-04-01",
          sex: "男",
          age: 15,
          grade: "高二",
          class: "3班",
          year: "2018~2019学年 上学期",
        },
      ],
      // =列表新增/修改数据
      editObj: {
        name: "",
        studentCard: "",
        startSchool: "",
        sex: "",
        age: null,
        grade: "",
        class: "",
        year: "",
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
      },
      // value1: "",
      // value2: "",
    };
  },
  methods: {
    // 删除数据
    removeData(index) {
      this.$confirm("此操作将永久删除该信息, 是否继续?")
        .then((_) => {
          console.log(index);
          this.tableData.splice(index, 1);
          this.$message({
            message: "操作成功！",
            type: "success",
          });
        })
        .catch((_) => {});
    },
    // 弹窗是否显示
    handleClose() {
      this.dialogVisible = false;
    },
    // 修改数据
    editData(row, index) {
      if (this.$refs["editObj"] !== undefined) {
        this.$refs["editObj"].resetFields();
      }
      let that = this;
      this.isAdd = false;
      this.currentIndex = index;
      this.titleMsg = "编辑学生信息";
      this.editObj = {
        name: row.name,
        studentCard: row.studentCard,
        startSchool: row.startSchool,
        sex: row.sex,
        age: row.age,
        grade: row.grade,
        class: row.class,
        year: that.tableData[index].year,
      };
      this.dialogVisible = true;
      // console.log(this.editObj);
      // console.log(this.currentIndex);
    },
    // 提交数据
    sbumitData(formName) {
      if (this.isAdd == true) {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.dialogVisible = false;
            this.tableData.unshift(this.editObj);
            this.$message({
              message: "操作成功!",
              type: "success",
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      } else {
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.dialogVisible = false;
            this.tableData.splice(this.currentIndex, 1, this.editObj);
            this.$message({
              message: "操作成功!",
              type: "success",
            });
          } else {
            console.log("error submit!!");
            return false;
          }
        });
      }
    },
    //新增数据
    addData() {
      if (this.$refs["editObj"] !== undefined) {
        this.$refs["editObj"].resetFields();
      }

      this.isAdd = true;
      this.titleMsg = "新增学生信息";
      this.editObj = {
        name: "",
        studentCard: "",
        startSchool: "",
        sex: "",
        age: "",
        grade: "",
        class: "",
        year: "",
      };
      this.dialogVisible = true;
    },
    //过滤数据
    filterYear() {
      this.tableData = this.tableData.filter(
        (item) => item.year == this.tableData.year
      );
      let obj = {};
      obj = this.options.find((item) => {
        return item.value === this.value;
      });
      console.log(obj.label);
      this.tableData = this.tableData.filter((item) => item.year == obj.label);
    },
    //获取多选数据
    selectionChange(val) {
      this.multipleSelection = val;
      // console.log(this.multipleSelection);
    },
    //删除多选数据
    removeMulSelectData() {
      if (this.multipleSelection.length == 0) {
        this.$message({
          message: "至少选择一条信息",
          type: "warning",
        });
      } else {
        this.$confirm(
          `此操作将永久删除这 ${this.multipleSelection.length} 条信息, 是否继续?`,
          "提示",
          {
            confirmButtonText: "确定",
            cancelButtonText: "取消",
            type: "warning",
          }
        )
          .then(() => {
            //遍历获得多选选中的index值
            this.multipleSelection.forEach((value, index) => {
              //遍历多选框获取的数据
              this.tableData.forEach((v, i) => {
                //遍历数据表，任意一个属性值相同，则说明该数据被选中，其i则为索引值
                if (value.studentCard == v.studentCard) {
                  this.tableData.splice(i, 1);
                }
              });
            });
            this.$message({
              type: "success",
              message: "操作成功!",
            });
            // console.log("操作成功");
          })
          .catch(() => {});
      }
    },
    //分页方法
    handleSizeChange(val) {
      // 改变每页显示的条数
      this.PageSize = val;
      // 注意：在改变每页显示的条数时，要将页码显示到第一页
      this.currentPage = 1;
    },
    //分页方法
    handleCurrentChange(val) {
      // 改变默认的页数
      this.currentPage = val;
    },
  },
  created: function () {

  },
};
</script>

<style scoped>
#el-table {
  position: relative;
  top: 20px;
  margin: 0 auto;
  width: 95%;
}

#list {
  position: relative;
  top: -10px;
}

.el-row {
  margin-bottom: 20px;
}

#el-row {
  position: relative;
  top: 10px;
}

#filter-data {
  position: absolute;
  right: 0px;
  top: 10px;
}

.el-select {
  margin-right: 20px !important;
}

#pagination {
  position: absolute;
  right: 0;
  bottom: -45px;
}
</style>
